









//@ts-nocheck
import { mapGetters, mapMutations as mapSetters } from "vuex";
export default {

    mounted() 
    {
        const IsMobile = window.innerWidth < 600;
        if(IsMobile)
        {
            const First = document.querySelector(".floor-selector-item[first]");
            First.style.setProperty("border-radius", "4px 0px 0px 4px");
            const Last = document.querySelector(".floor-selector-item[last]");
            Last.style.setProperty("border-radius", "0px 4px 4px 0px");
        }

        this.$refs.floor.onchange = (event) => { this.OnSelectFloor(event.srcElement); }
        // select default floor
        this.OnSelectFloor(this.$refs.DefaultFloor);
    },

    computed: 
    {
        ...mapGetters([
            "Floor"
        ]),

        FloorStyle()
        {
            const IsMobile = window.innerWidth < 600;
            return `
                flex-direction: ${IsMobile ? 'row' : 'column'};
                ${IsMobile ? 'bottom: 166px' : 'left: 16px'};
                ${IsMobile ? 'left: 50vw' : 'top: 50vh'};
                ${IsMobile ? 'transform: translateX(-50%)' : 'transform: translateY(-50%)'};
            `;
        },

        FloorItemStyle()
        {
            const IsMobile = window.innerWidth < 600;
            return `
                width: ${IsMobile ? '56' : '48'}px;
                height: ${IsMobile ? '48' : '56'}px;
            `;
        }
    },

    methods: 
    {
        ...mapSetters([
            "setFloor"
        ]),

        OnSelectFloor(Input: HTMLElement)
        {
            const Floor = Input.parentElement;
            if(Floor)
            {
                // remove previously selected floor
                this.$refs.floor.querySelectorAll(".floor-selector-item").forEach(Elem => { Elem.removeAttribute("checked"); })
                Floor.setAttribute("checked", true);
                this.setFloor(Floor.getAttribute("value"));
            }
        }
    }
}
