<template>
    <div class="vld-parent">
        <VueLoading :active="isLoading" is-full-page background-color="transparent" opacity=1 lock-scroll blur="2px" loader="none" />
        <div class="overlay" :style="cssVars">
            <LinearLoadingIndicator text="Loading fresh air for the Swedish Pavillion ..." />
        </div>
    </div>
</template>

<script>
import VueLoading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import LinearLoadingIndicator from "@/components/LinearLoadingIndicator.vue"

export default {
    props: ["isLoading"],
    components: {
        VueLoading,
        LinearLoadingIndicator
    },
    computed: {
        cssVars() {
            return {
                '--opacity': this.isLoading ? 1 : 0,
            }
        }
    },
}
</script>

<style scoped>
.vld-parent {
